import { Component, OnInit, ViewChild } from '@angular/core';
import { Location } from "@angular/common";
import {ActivatedRoute} from '@angular/router';
import {FormService} from '../../services/form/form.service';
import {SubmissionService} from '../../services/submission/submission.service';
import {OfflineService} from '../../services/offline/offline.service';
import {AuthenticationService} from '../../services/authentication/authentication.service';
import {UploadService} from '../../services/upload/upload.service';
import { ToastController, ModalController,AlertController, LoadingController } from '@ionic/angular';
import { Router, RouterEvent, NavigationEnd } from '@angular/router';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import { Storage } from '@ionic/storage';
import SignaturePad from 'signature_pad';
import * as moment from 'moment';

@Component({
    selector: 'app-form',
    templateUrl: './form.page.html',
    styleUrls: ['./form.page.scss'],
})
export class FormPage implements OnInit {
    public loading:boolean;
    
    public user:any;
    
    public form:any;
    public fieldGroups:Array<any>;
    public groupedFields:any;
    public activeGroup:any;
    public activeGroupIndex:number;
    
    public submission:any;

    public width:any;
    
    

    constructor(private formService:FormService, public toastController: ToastController, public modalController: ModalController,
        private router: Router, private authenticationService:AuthenticationService, private alertCtrl: AlertController, 
        private route:ActivatedRoute, private iab: InAppBrowser, public loadingController: LoadingController, private uploadService:UploadService,
        private submissionService:SubmissionService, private location: Location, private offlineSerivce: OfflineService) {

        this.loading = true;
        
        let formId = this.route.snapshot.params['id'];
        
        this.form = {id:formId};
        this.fieldGroups = [];
        this.groupedFields = {};
        this.getForm();
        
        this.user = {};
        this.authenticationService.getUserData().subscribe((data) => {

            this.user = data;
        });
        
        this.submission = {};
        
        
        
        
        
    }
        
        
        
    ngOnInit() {    
        
        
    }
    
    
    public submit(){
        
        this.submission.loading = true;

        
        
        for (let fieldGroup of this.fieldGroups){
            for (let field of this.groupedFields[fieldGroup.id]){
                if (field.field_type === "question_group"){
                    
                    this.submission[field.id] = JSON.stringify(field);
                    
                }
                
                else if (field.field_type === "draw"){
                    this.submission[field.id] = field.signaturePad.toDataURL();
                }
                else if (field.field_type === "signature"){
                    this.submission[field.id] = field.signaturePad.toDataURL();
                }
            }
        }
        

        
        
        
        this.submissionService.createSubmission(this.submission, this.form.id).then(() => {
            
            this.submission.loading = false;
            
            this.alertCtrl.create({
                header: "Success",
                message: "Your submission has been saved successfully",
                buttons: [
                    {
                        text: 'Dismiss',
                        role: 'cancel'
                    }
                ]
            }).then((el) => {
                el.present(); 
            });    
            
            this.location.back();
            
                    
        }).catch(() => {
            //TODO:save as offline   
        
            this.offlineSerivce.saveSubmission({fields:this.submission, form_id:this.form.id});
        
            this.submission.loading = false;
        
            this.alertCtrl.create({
                header: "You're Offline",
                message: "Your submission has been saved and will be uploaded as soon as an internet connection is available.",
                buttons: [
                    {
                        text: 'Dismiss',
                        role: 'cancel'
                    }
                ]
            }).then((el) => {
                el.present(); 
            });          
        
             
            this.location.back();
            
            
        });
                
    }

    
    
    public getForm(){
        this.formService.getForm(this.form.id).then((data:any) => {

            
            this.setFormData(data);
            
            this.loading = false;    

        }).catch(() => {
        
        
            this.offlineSerivce.getForms().then((data) => {
                if (data){
                    for (let form of data){
                        if (form.id === this.form.id){
                            this.setFormData(form);
                        }
                    }
                }
            })
        
            this.loading = false;
        }); 

    }   
    
    
    
    private setFormData(data){

        this.form = data;
        this.fieldGroups = [];
        this.groupedFields = {};

        let unGrouped = 1;

        for (let field of this.form.fields){

            if (!field.field_group_id && this.groupedFields["ungrouped" + unGrouped]){                   
                this.groupedFields["ungrouped" + unGrouped].push(field);    
            }
            else if (!field.field_group_id && !this.groupedFields["ungrouped" + unGrouped]){
                this.groupedFields["ungrouped" + unGrouped] = [field];
                this.fieldGroups.push({name:"Step " + (this.fieldGroups.length + 1), id:"ungrouped" + unGrouped});
            }
            else if (this.groupedFields[field.field_group_id]){
                this.groupedFields[field.field_group_id].push(field);
                unGrouped += 1;
            }
            else{
                this.groupedFields[field.field_group_id] = [field];

                for (let fieldGroup of this.form.field_groups){
                    if (fieldGroup.id === field.field_group_id){
                        this.fieldGroups.push(fieldGroup);
                        break;
                    }
                }


                unGrouped += 1;
            }


            field.field_options = field.field_options ? JSON.parse(field.field_options) : [];

        }



        if (this.fieldGroups.length > 0){

            this.changeActiveGroup(this.fieldGroups[0].id);
        }        
    }
    
    
    
    public changeActiveGroup(fieldGroupId){
                
        this.activeGroup = fieldGroupId;
        this.activeGroupIndex = this.fieldGroups.findIndex(item => item.id === fieldGroupId);

        let fields = this.groupedFields[this.activeGroup];
        for (let field of fields){
            if ((field.field_type === "signature" || field.field_type === "draw") && !field.signaturePad){

                setTimeout(() => {
                    let signaturePadElement:any = document.querySelector("#canvas" + field.id);
                    field.signaturePad = new SignaturePad(signaturePadElement);
                });
            }             
        }
        
    }

    
    public getWidth() {
      return window.innerWidth;
    }    
    
    
    public drawUndo(field){
        if (!field.signaturePad){
            return;
        }
        
	let data = field.signaturePad.toData();
        if (data) {
            data.pop(); // remove the last dot or line
            field.signaturePad.fromData(data);
        }        
        
    }
    
    public drawClear(field){
        if (!field.signaturePad){
            return;
        }
        
	field.signaturePad.clear();      
        
    }
    
        
    public openFile(file){
        this.iab.create(file, '_system');
    } 
    
    public fileChangeListener(ev, field) {

        const files = ev.srcElement.files;
        
        field.file_name = files[0].name;        

        const formData: FormData = new FormData();

        const mediaFile: File = files[0];

        formData.append('media_file', mediaFile, mediaFile.name);

        let loading;
        this.loadingController.create({
          message: 'Your file is being uploaded. Please wait...',
          duration: 20000
        }).then((el) => {
            loading = el;
            el.present();
        });  
        

        
        
        this.uploadService.uploadFile(formData).then((data:any) => {

            this.submission[field.id] = data.file;

            loading.dismiss();
        }).catch((data) => {
            loading.dismiss();
        });

    }
    
    
    
    
    
    
}
