import { Component, OnInit, ViewChild, Input, OnChanges } from '@angular/core';
import {UploadService} from '../../services/upload/upload.service';
import { ToastController, ModalController,LoadingController } from '@ionic/angular';
import { InAppBrowser } from '@ionic-native/in-app-browser/ngx';
import SignaturePad from 'signature_pad';
import * as moment from 'moment';

@Component({
  selector: 'app-question-group',
  templateUrl: './question-group.component.html',
  styleUrls: ['./question-group.component.scss'],
})
export class QuestionGroupComponent implements OnInit {
    
    public fields:Array<any>;
    public submission:any;
    @Input() field: any; 
    
    constructor(public toastController: ToastController, public modalController: ModalController,private iab: InAppBrowser, 
        public loadingController: LoadingController, private uploadService:UploadService) { 
        
        
        
        this.fields = [];
        this.submission = {};    

        
    }

    ngOnInit() {
        this.field.submission = {};        
        this.fields = this.field ? (this.field.field_options ? this.field.field_options : this.field.options) : [];
        this.setFields();
        
        
        
    }
    
    ngOnChanges() {
    }
    
    public setFields(){
        
        let count = 1;
        for (let field of this.fields){
            
            field.name = field.label ? field.label : ("Question " + count);
            
            if ((field.field_type === "signature" || field.field_type === "draw") && !field.signaturePad){

                setTimeout(() => {
                    let signaturePadElement:any = document.querySelector("#canvas" + field.id);
                    field.signaturePad = new SignaturePad(signaturePadElement);
                });
            }  
            
            
            field.options = field.options ? JSON.parse(field.options) : [];
            
            count += 1;
                       
        }        
        
        
    }
    
    public getWidth() {
      return window.innerWidth;
    }    
    
    
    public drawUndo(field){
        if (!field.signaturePad){
            return;
        }
        
	let data = field.signaturePad.toData();
        if (data) {
            data.pop(); // remove the last dot or line
            field.signaturePad.fromData(data);
        }        
        
    }
    
    public drawClear(field){
        if (!field.signaturePad){
            return;
        }
        
	field.signaturePad.clear();      
        
    }
    
        
    public openFile(file){
        this.iab.create(file, '_system');
    } 
    
    public fileChangeListener(ev, field) {

        const files = ev.srcElement.files;
        
        field.file_name = files[0].name;        

        const formData: FormData = new FormData();

        const mediaFile: File = files[0];

        formData.append('media_file', mediaFile, mediaFile.name);

        let loading;
        this.loadingController.create({
          message: 'Your file is being uploaded. Please wait...',
          duration: 20000
        }).then((el) => {
            loading = el;
            el.present();
        });  
        

        
        
        this.uploadService.uploadFile(formData).then((data:any) => {

            this.submission[field.id] = data.file;

            loading.dismiss();
        }).catch((data) => {
            loading.dismiss();
        });

    }
    
    
    
        
    
    
    

}
